.mq-parser {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 0;
  height: 0;
  background: #000;
  color: #fff;
  font-family: 'Courier New', Courier, monospace;
  font-size: 80%;
  font-weight: bold;
  overflow: hidden;
  z-index: 999; }
  .mq-parser.mq-parser--debug {
    width: auto;
    height: auto; }
  .mq-parser * {
    display: none;
    width: 100%;
    padding: .5em 1em;
    border-bottom: 1px solid #333333; }
  @media (min-width: 300px) {
    .mq-parser .small {
      display: block; } }
  @media (min-width: 600px) {
    .mq-parser .medium {
      display: block; } }
  @media (min-width: 1000px) {
    .mq-parser .large {
      display: block; } }
  @media (min-width: 1200px) {
    .mq-parser .x-large {
      display: block; } }
  .mq-parser::after {
    display: none;
    content: "small,medium,large,x-large"; }
  .mq-parser::before {
    display: block;
    padding: .5em 1em;
    background: #4d4d4d;
    content: 'Current Breakpoints'; }

