.image-grid {
  display: flex;
  flex-wrap: wrap;
  max-width: 1500px;
  margin: 0 auto; }

.image-grid-item {
  width: 50%; }
  .image-grid--non-amp .image-grid-item {
    position: relative;
    width: 50%;
    height: 0;
    padding-bottom: 37.5%; }
    .image-grid--non-amp .image-grid-item::before {
      display: flex;
      justify-content: center;
      align-items: center;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: #fff;
      content: 'LOADING...';
      border: 2px dashed #dadada;
      z-index: 1; }
    .image-grid--non-amp .image-grid-item.image-loaded::before {
      display: none; }
  .image-grid-item img {
    display: block; }
    .image-grid--non-amp .image-grid-item img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%; }

.nav-list,
